body {
  // padding-top: $header-height;

  --animate-duration: 1s;
  --animate-delay: 0s;
  --animate-repeat: 1;
}

body.mobile {
  padding-top: 72px;

  @include media-breakpoint-up(sm) {
    padding-top: 0;
  }
}

html,
body {
  height: 100%;
}

a {
  text-decoration: none;
  color: $primary;
  transition: $transition-base;

  &:active {
    outline: 0;
  }
}

::selection {
  background: rgba($blue-light, 0.2);
}

.j-link {
  display: flex;
  align-items: center;
  padding: space(3) 0;

  &:hover {
    j-icon svg.stroke.color-primary {
      stroke: $link-hover-color;
    }
  }
}

.link-primary {
  cursor: pointer;
  transition: $transition-base;
  color: $primary;

  &:hover {
    color: $link-hover-color;
  }
}

.link-blue {
  transition: $transition-base;
  cursor: pointer;
  color: $blue-light;

  &:hover {
    color: $primary;
  }
}

.link-gray {
  cursor: pointer;
  transition: $transition-base;
  color: $gray-600;

  &:hover {
    color: $primary;
  }
}

html,
body {
  height: 100%;
}

.bg-main {
  background: $gray-200;
}
