// Variables
//
// Variables should follow the `$component-state-property-size` formula for
// consistent naming. Ex: $nav-link-disabled-color and $modal-content-box-shadow-xs.

// Color system

$white: #fff;
$gray-100: #faf7f7;
$gray-200: #f6f7f8;
$gray-300: #eceef1;
$gray-400: #d6dadf;
$gray-500: #b7bec5;
$gray-600: #666c72;
$gray-700: #474b52;
$gray-800: #e9ecf1;
$gray-900: #dbdfe2;
$gray-1000: #828282;
$black: #1a1c1f;

$grays: ();
$grays: map-merge(
  (
    '100': $gray-100,
    '200': $gray-200,
    '300': $gray-300,
    '400': $gray-400,
    '500': $gray-500,
    '600': $gray-600,
    '700': $gray-700,
    '800': $gray-800,
  ),
  $grays
);

$blue: #204177;
$blue-light: #1586bb;
$blue-white: #f5f9fd;
$orange: #ef5630;
$orange-dark: #d43e19;
$orange-light: #f7ab98;
$yellow: #ef8f00;
$green: #239a54;
$green-light: #74ba72;
$red: #ff0000;
$red-light: #ffefef;

$gray: $gray-500;
$gray-dark: $gray-600;
$gray-light: $gray-400;

$colors: ();
$colors: map-merge(
  (
    'blue': $blue,
    'blue-light': $blue-light,
    'blue-white': $blue-white,
    'orange': $orange,
    'orange-dark': $orange-dark,
    'orange-light': $orange-light,
    'yellow': $yellow,
    'green': $green,
    'green-light': $green-light,
    'red': $red,
    'red-light': $red-light,
    'black': $black,
    'black-light': $gray-700,
    'gray': $gray-500,
    'gray-dark': $gray-600,
    'gray-light': $gray-400,
  ),
  $colors
);

$primary: $orange;
$secondary: $blue;
$success: $green;
$info: $blue-light;
$warning: $yellow;
$danger: $red;
$light: $gray-500;
$dark: $black;

$theme-colors: ();
$theme-colors: map-merge(
  (
    'primary': $primary,
    'secondary': $secondary,
    'success': $success,
    'info': $info,
    'warning': $warning,
    'danger': $danger,
    'light': $light,
    'dark': $dark,
    'white': $white,
  ),
  $theme-colors
);

