// Spacing
//
// Control the default styling of most Bootstrap elements by modifying these
// variables. Mostly focused on spacing.
// You can add more entries to the $spacers map, should you need more variation.

$spacer: 1rem;
$spacers: ();
$spacers: map-merge(
  (
    0: 0,
    // 0px
    1: $spacer * 0.25,
    // 4px xxs,
    2: $spacer * 0.5,
    // 8px xs,
    3: $spacer,
    // 16px sm
    4: $spacer * 1.5,
    // 24px md,
    5: $spacer * 2,
    // 32px lg,
    6: $spacer * 2.5,
    // 40px xl,
    7: $spacer * 3.5,
    // 56px xxl
    8: $spacer * 4,
    // 64px xxl
  ),
  $spacers
);

// Body
//
// Settings for the `<body>` element.

$body-bg: $white;
$body-color: $dark;

// Links
//
// Style anchor elements.

$link-color: theme-color('primary');
$link-decoration: none;
$link-hover-color: $orange-dark;
$link-hover-decoration: none;

// Paragraphs
//
// Style p element.

$paragraph-margin-bottom: 1rem;

// Grid breakpoints
//
// Define the minimum dimensions at which your layout will change,
// adapting to different screen sizes, for use in media queries.

$grid-breakpoints: (
  xs: 0,
  sm: 670px,
  md: 960px,
  lg: 1280px,
  xl: 1920px,
);

// Grid containers
//
// Define the maximum width of `.container` for different screen sizes.

$container-max-widths: (
  sm: 540px,
  md: 720px,
  lg: 960px,
  xl: 1280px,
);

// Grid columns
//
// Set the number of columns and specify the width of the gutters.

$grid-columns: 12;
$grid-gutter-width: 30px;
$grid-row-columns: 6;

// Components
//
// Define common padding and border radius sizes and more.

$line-height-lg: 1.5;
$line-height-sm: 1.5;

$border-width: 1px;
$border-color: $gray-300;

$border-radius: 0.5rem;
$border-radius-md: remy(12);
$border-radius-lg: 2.5rem;
$border-radius-sm: 0.2rem;

// L0
$box-shadow-xs: 0px 0px 0px rgba(0, 0, 0, 0.05);
// L1
$box-shadow-sm: 0px 0px 1px rgba(0, 0, 0, 0.04), 0px 0px 2px rgba(0, 0, 0, 0.06),
  0px 4px 8px rgba(0, 0, 0, 0.04);
// L2
$box-shadow: 0px 0px 1px rgba(0, 0, 0, 0.04), 0px 2px 6px rgba(0, 0, 0, 0.04),
  0px 10px 20px rgba(0, 0, 0, 0.04);
// L3
$box-shadow-lg: 0px 14px 16px rgba(0, 0, 0, 0.05);
// L4
$box-shadow-xl: 0px 0px 1px rgba(0, 0, 0, 0.04), 0px 4px 8px rgba(0, 0, 0, 0.04),
  0px 16px 24px rgba(0, 0, 0, 0.04), 0px 24px 32px rgba(0, 0, 0, 0.04);

$component-active-color: $white;
$component-active-bg: theme-color('primary');

$transition-base: all 0.2s ease-in-out;
$transition-fade: opacity 0.15s linear;
$transition-collapse: height 0.35s ease;
$transition-max-height: all 0.6s ease;

// Typography
//
// Font, line-height, and color for body text, headings, and more.

$font-family-sans-serif: 'Roboto', sans-serif;
$font-family-monospace: SFMono-Regular, Menlo, Monaco, Consolas,
  'Liberation Mono', 'Courier New', monospace !default;
$font-family-base: $font-family-sans-serif;

$font-size-base: 1rem; // 16px
$font-size-lg: $font-size-base * 1.25 !default; // 20px
$font-size-sm: $font-size-base * 0.875 !default; // 14px
$font-size-xs: remy(12);

$font-weight-lighter: lighter;
$font-weight-light: 300;
$font-weight-normal: 400;
$font-weight-bold: 500;
$font-weight-bolder: bolder;

$font-weight-base: $font-weight-normal;
$line-height-base: 1.5;

$h1-font-size: remy(30);
$h2-font-size: remy(26);
$h3-font-size: remy(24);
$h4-font-size: remy(22);
$h5-font-size: remy(20);
$h6-font-size: remy(18);

$h1-font-sizes: (
  xs: 30px,
  sm: 36px,
  md: 48px,
  lg: 48px,
  xl: 60px,
);

// h2 sizes
$h2-font-sizes: (
  xs: 26px,
  sm: 30px,
  md: 40px,
  lg: 44px,
  xl: 54px,
);

// h3 sizes
$h3-font-sizes: (
  xs: 24px,
  sm: 26px,
  md: 32px,
  lg: 36px,
  xl: 44px,
);

// h4 sizes
$h4-font-sizes: (
  xs: 22px,
  sm: 24px,
  md: 24px,
  lg: 28px,
  xl: 28px,
);

// h5 sizes
$h5-font-sizes: (
  xs: 20px,
  sm: 22px,
  md: 22px,
  lg: 24px,
  xl: 24px,
);

// h6 sizes
$h6-font-sizes: (
  xs: 18px,
  sm: 18px,
  md: 18px,
  lg: 20px,
  xl: 20px,
);

$headings-margin-bottom: $spacer / 2 !default;
$headings-font-family: null;
$headings-font-weight: 500;
$headings-font-weight-light: 400;
$headings-line-height: 1.5;
$headings-color: null;

$p0-font-sizes: (
  xs: 16px,
  sm: 16px,
  md: 16px,
  lg: 16px,
  xl: 16px,
);

$p1-font-sizes: (
  xs: 16px,
  sm: 16px,
  md: 18px,
  lg: 18px,
  xl: 18px,
);

$p2-font-sizes: (
  xs: 14px,
  sm: 14px,
  md: 16px,
  lg: 16px,
  xl: 16px,
);

$p3-font-sizes: (
  xs: 12px,
  sm: 12px,
  md: 14px,
  lg: 14px,
  xl: 14px,
);

$p4-font-sizes: (
  xs: 10px,
  sm: 10px,
  md: 12px,
  lg: 12px,
  xl: 12px,
);

$small-font-size: $font-size-xs;

$text-muted: $gray-600;

