$j-input-label-position-top-active: 10px;
$j-input-link-position-top: 16px;
$j-input-label-position-left: $j-input-padding-x + $j-input-border-width;
$j-input-label-line-height: $j-input-line-height;
$j-input-label-color: $gray-dark;

$j-input-padding-top: 27px;
$j-input-padding-bottom: 10px;
$j-input-margin-bottom: 16px;
$j-input-placeholder-color: $gray;
$j-input-placeholder-position: $j-input-padding-top + $j-input-border-width;
$j-input-char-length: 8.99px;

$j-input-button-position: 20px;
$j-input-shadow-position: $j-input-label-position-left;

$j-input-label-z-index: 0;
$j-input-shadow-z-index: 1;
$j-input-z-index: 2;
$j-input-link-z-index: 3;
$j-input-button-z-index: 4;

$box-shadow-to-top-lg: 0 -1rem 3rem rgba($black, 0.175);

$enable-pointer-cursor-for-buttons: true;
