.bs-datepicker.theme-jysan {
  .bs-datepicker-container {
    padding: 14px 10px;
  }

  .bs-datepicker-head {
    padding: 8px 0;
    height: auto;

    &::after {
      display: none;
    }

    bs-datepicker-navigation-view {
      display: flex;
      width: 100%;
    }

    button.current {
      color: $dark;
      margin-left: auto;
      margin-right: auto;

      &:hover {
        color: $primary;
        background: transparent;
      }

      &::after {
        content: ' .';
        display: inline;
      }
    }

    button.next,
    button.previous {
      color: $primary;
      display: flex;
      align-items: center;
      justify-content: center;

      &:hover {
        color: $primary;
        background: transparent;
      }

      &::before {
        content: '';
        width: 16px;
        height: 16px;
      }

      span {
        display: none;
      }
    }

    button.next {
      margin-left: auto;

      &::before {
        background-image: url('/assets/icons/arrow-right-orange.svg');
      }
    }

    button.previous {
      margin-right: auto;

      &::before {
        background-image: url('/assets/icons/arrow-left-orange.svg');
      }
    }
  }

  .bs-datepicker-body {
    padding: 0;
    border: 0;

    table {
      td {
        color: $dark;
        font-size: $small-font-size;

        &:hover {
          span {
            background: transparent;
            color: $primary;
          }
        }

        span {
          border-radius: 4px;
        }

        span.selected,
        &.is-highlighted .span {
          &,
          &:hover {
            background: $primary;
            color: $white;
          }
        }
      }
    }
  }

  bs-years-calendar-view {
    .bs-datepicker-head {
      button.current {
        &::after {
          display: none;
        }
      }
    }
  }
}

ngb-datepicker.theme-jysan {
  border: 0;

  .ngb-dp-header,
  .ngb-dp-weekdays {
    background: $white;
  }
  ngb-datepicker-navigation-select {
    .custom-select + .custom-select {
      //   border: 0;
      margin-left: 10px;
      //   background: none;
    }
  }

  .ngb-dp-header {
    padding-top: 1.25rem;
  }

  input.form-control {
    color: transparent;
    caret-color: transparent;
    cursor: pointer;
    user-select: none;
    font-size: 0px;
  }

  .dropdown-menu {
    padding: calc($grid-gutter-width / 2);
    right: 0;
    margin-top: 0px;
    border: 0px;
    box-shadow: $box-shadow;
    border-radius: 0 0 $j-input-border-radius $j-input-border-radius;
  }
  .ngb-dp-month:nth-last-child(2),
  .ngb-dp-month:nth-last-child(2) + .ngb-dp-month {
    width: 50%;
  }
  .ngb-dp-week {
    margin-bottom: remy(12);
    &:last-child {
      margin-bottom: 0;
    }
  }
  .ngb-dp-header,
  .ngb-dp-month-name,
  .ngb-dp-weekdays {
    background: none;
  }
  .ngb-dp-month {
    padding: 1rem;
  }
  .ngb-dp-weekdays {
    border: none;
  }
  .ngb-dp-weekday {
    color: $gray-600;
    font-style: normal;
  }
  .ngb-dp-day,
  .ngb-dp-week-number,
  .ngb-dp-weekday {
    flex-shrink: 0;
    width: 2rem;
    height: 2rem;
    font-size: $small-font-size;
  }
  .ngb-dp-today .btn-light {
    border: 1px solid $gray-600;
  }
  .ngb-dp-day {
    border-radius: 50%;
    &:focus {
      background: none;
      outline: none;
      box-shadow: 0px 0px 0px 1px rgba($primary, 0.75);
    }
    .btn-light {
      width: 2.5em;
      height: 2.5em;
      line-height: 2.5;
      border-radius: 50%;
      &:focus {
        outline: none;
      }
      &:hover,
      &:active,
      &.active {
        background: none;
      }
    }
  }
  .ngb-dp-content {
    justify-content: center;
  }

  .ngb-dp-day.disabled,
  .ngb-dp-day.hidden {
    opacity: 0.5;
  }
}
