@use "sass:map";

h1 {
  @each $size, $g-size in $grid-breakpoints {
    @include media-breakpoint-up($size) {
      font-size: map.get($h1-font-sizes, #{$size});
    }
  }
}

h2 {
  @each $size, $g-size in $grid-breakpoints {
    @include media-breakpoint-up($size) {
      font-size: map.get($h2-font-sizes, #{$size});
    }
  }
}

h3 {
  @each $size, $g-size in $grid-breakpoints {
    @include media-breakpoint-up($size) {
      font-size: map.get($h3-font-sizes, #{$size});
    }
  }
}

h4 {
  @each $size, $g-size in $grid-breakpoints {
    @include media-breakpoint-up($size) {
      font-size: map.get($h4-font-sizes, #{$size});
    }
  }
}

h5 {
  @each $size, $g-size in $grid-breakpoints {
    @include media-breakpoint-up($size) {
      font-size: map.get($h5-font-sizes, #{$size});
    }
  }
}

h6 {
  @each $size, $g-size in $grid-breakpoints {
    @include media-breakpoint-up($size) {
      font-size: map.get($h6-font-sizes, #{$size});
    }
  }
}

h1,
h2,
h3,
h4,
h5,
h6 {
  &.light {
    font-weight: $headings-font-weight-light;
  }
}

.p0 {
  @include p0;
}

.p1 {
  @include p1;
}

.p2 {
  @include p2;
}

.p3 {
  @include p3;
}

.p4 {
  @include p4;
}

strong, .text-strong {
  font-weight: $font-weight-bold;
}

.text-small {
  font-size: $font-size-sm;
}

.text-blue {
  color: $blue-light;
}

.text-gray {
  color: $gray-600;
}

.text-disabled {
  color: $gray-500;
}

.font-weight-medium {
  font-weight: 500;
}

mark,
.mark {
  padding: 0.2em 0;
  background-color: rgba($blue-light, 0.2);
}


.no-wrap{
  white-space: nowrap;
}