.j-option-label {
  background-color: $gray-200;
  padding: space(3);
  border-radius: $input-border-radius;
  display: block;
  transition: $transition-base;
  // @include truncate-text;
  & + & {
    margin-top: space(2);
  }
  &.j-option-label:hover,
  &.j-option-label-selected,
  &.j-option-label-marked {
    background-color: $gray-800;
  }
}

.account-pill {
  padding: 0.6em 1em 0.4em;
  display: flex;
  border-radius: $input-border-radius;
  align-items: center;
  &.account-pill_gray {
    background-color: $gray-200;
  }
}
.account-pill--content {
  padding-left: 1em;
  flex: 1;
  p {
    @include truncate-text;
    margin: 0px;
  }
}
.account-pill--logo {
  line-height: 0px;
  margin-bottom: 0.3em;
}
.account-pill--suffix {
  display: inline-flex;
  align-items: center;
}
.j-option:hover,
.j-option-selected,
.j-option-marked {
  .j-option-label,
  .account-pill {
    background-color: $gray-300;
  }
}
