.btn-header {
  height: 48px;
  padding: 0 1rem;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  margin-left: 1rem;
  font-weight: $font-weight-bold;

  &:first-child {
    margin-left: 0;
  }

  .header-icon {
    @include media-breakpoint-up(sm) {
      margin-right: 8px;
    }
  }
}

.header-icon {
  width: 24px;
  height: 24px;
  display: inline-flex;
  justify-content: center;
  align-items: center;
}

.header-title {
  margin-bottom: space(4);

  @include media-breakpoint-up(md) {
    margin-bottom: 0;
  }
}

.header-buttons {
  margin-left: auto;
  display: flex;
}
