.j-tabs {
  position: relative;
  height: 70px;
  overflow: hidden;
  background: $white;
  padding: space(3) space(4) space(0) space(4);
  border-radius: $border-radius $border-radius 0 0;
}

.j-tabs__list {
  // position: absolute;
  display: flex;
  // min-width: 100%;
}

.j-tabs__item {
  position: relative;
  white-space: nowrap;
  background: none;
  border: 0;
  outline: 0;
  display: block;
  padding: 12px 0;
  color: $text-muted;
  margin-right: space(6);
  font-size: $h6-font-size;

  &::after {
    display: none;
    position: absolute;
    content: '';
    left: 0;
    right: 0;
    height: 2px;
    bottom: -3px;
    background: $primary;
  }

  &.active {
    color: $dark;
    font-weight: $font-weight-bold;

    &::after {
      display: block;
    }
  }
  
  &--soon {
    user-select: none;
    cursor: not-allowed;

    .j-tabs__label {
      opacity: 0.5;
    }
  }

  &[disabled] {
    user-select: none;
    cursor: not-allowed;
    opacity: 0.5;
  }
}

.j-tabs__add {
  padding: 0;
  display: flex;
  align-items: center;
}

.j-tabs__add-icon {
  @include media-breakpoint-up(md) {
    margin-right: 8px;
  }
}

.j-tabs__badge {
  position: absolute;
  top: 0px;
  right: 0;
  transform: translate(75%, -25%);

  min-width: 30px;
  min-height: 18px;

  .spinner {
    div {
      width: 4px;
      height: 4px;
      margin-right: 2px;
    }
  }
}
