.layout-home {
  overflow-x: hidden;
  display: flex;
  align-items: stretch;
  min-height: calc(100vh - #{$header-height});
  flex-direction: column;
  justify-content: center;
  background: $gray-200;

  @include media-breakpoint-up(lg) {
    background: $white;
    min-height: 100vh;
  }
}

.layout-home--content {
  margin-top: $header-height;
  position: relative;
  z-index: 1;
  background: $gray-200;
  min-height: calc(100vh - #{$header-height});
  display: flex;
  align-items: stretch;

  @include media-breakpoint-up(md) {
    margin-top: $header-height-md;
    min-height: calc(100vh - #{$header-height-md});
  }

  @include media-breakpoint-up(lg) {
    margin-top: 0;
    min-height: 100vh;
    margin-left: 280px;
    box-shadow: $box-shadow-xl;
    border-radius: 40px 0px 0px 40px;
  }
}

.layout-home--header {
  background: $white;
  position: fixed;
  top: 0px;
  left: 0;
  z-index: 10;
  width: 100%;

  @include media-breakpoint-up(lg) {
    width: $header-width;
    height: 100vh;
    bottom: 0;
    z-index: 0;
  }
}



.menu-enabled .layout-home--header {
  z-index: 10;
}
