.j-uploading-files {
  padding: 24px 0;
  margin: 0;
}

.j-uploading-files__item {
  display: flex;
  align-items: center;
  margin-bottom: 12px;

  &:last-child {
    margin-bottom: 0;
  }
}

.j-uploading-files__delete {
  margin-left: auto;
  padding: 0;
  cursor: pointer;
  color: $gray-500;

  &:hover {
    color: $primary;
  }
}

.j-uploading-files__icon {
  align-self: start;
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-right: 12px;
  flex-shrink: 0;
}

.j-uploading-files__icon--loading {
  width: 40px;
  height: 40px;
}

.j-uploading-files__progress {
  position: absolute;
  display: block;
  top: 0;
  left: 0;
}
