$zoomScale: 0.9;

@-webkit-keyframes zoomInJ {
  from {
    opacity: 0;
    -webkit-transform: scale3d($zoomScale, $zoomScale, $zoomScale);
    transform: scale3d($zoomScale, $zoomScale, $zoomScale);
  }

  100% {
    opacity: 1;
  }
}

@keyframes zoomInJ {
  from {
    opacity: 0;
    -webkit-transform: scale3d($zoomScale, $zoomScale, $zoomScale);
    transform: scale3d($zoomScale, $zoomScale, $zoomScale);
  }

  100% {
    opacity: 1;
  }
}

.zoomInJ {
  -webkit-animation-name: zoomInJ;
  animation-name: zoomInJ;
}

@-webkit-keyframes zoomOutJ {
  from {
    opacity: 1;
  }

  50% {
    opacity: 0;
    -webkit-transform: scale3d($zoomScale, $zoomScale, $zoomScale);
    transform: scale3d($zoomScale, $zoomScale, $zoomScale);
  }

  to {
    opacity: 0;
  }
}

@keyframes zoomOutJ {
  from {
    opacity: 1;
  }

  50% {
    opacity: 0;
    -webkit-transform: scale3d($zoomScale, $zoomScale, $zoomScale);
    transform: scale3d($zoomScale, $zoomScale, $zoomScale);
  }

  to {
    opacity: 0;
  }
}

.zoomOutJ {
  -webkit-animation-name: zoomOutJ;
  animation-name: zoomOutJ;
}
