.nav-menu {
  width: 100%;
  .tree > li {
    padding-left: 1em;
    margin-top: 10px;
  }
  .tree_root {
    > li > .nav-link {
      position: relative;
      &:before {
        position: absolute;
        left: -1em;
        top: 50%;
        bottom: 50%;
        background-color: $primary;
        width: 0;
        content: '';
      }
      &.active {
        font-weight: 500;
        &:before {
          top: 0;
          bottom: 0;
          width: 2px;
        }
      }
    }
  }
  .nav-link {
    color: $gray-600;
    &.active {
      color: $black;
    }
  }
}
