.filter-btn {
  @include button-variant($white, $gray-400);
  margin-right: 0.5rem;
  margin-bottom: 0.5rem;
  box-shadow: $box-shadow-sm;
  color: $gray-600;
  height: 35px;
  position: relative;

  &,
  &:hover,
  &:focus,
  &:active {
    color: $gray-600 !important;
    transform: scale(1) !important;
  }

  &.active {
    @include button-variant($gray-700, $gray-700);
    box-shadow: none;

    &,
    &:hover,
    &:focus,
    &:active {
      transform: scale(1);
      color: $white !important;
    }
  }

  .filter-badge {
    min-width: 30px;
    min-height: 18px;
    position: absolute;
    display: block;
    right: -0.5rem;
    top: -0.5rem;
    .spinner {
      div {
        width: 4px;
        height: 4px;
        margin-right: 2px;
      }
    }
  }
}

.filter-header {
  margin-bottom: 1rem;

  @include media-breakpoint-up(md) {
    display: flex;
    align-items: center;
  }
}

.filter-groups {
  display: flex;
  align-items: center;
}

.filter-group {
  display: flex;
  position: relative;

  @include media-breakpoint-up(sm) {
    padding: 0 0.5rem;
    margin-right: 1px;
  }

  &::after {
    content: '';
    display: block;
    position: absolute;
    height: 20px;
    right: -1px;
    width: 1px;
    background: $gray;
    top: 50%;
    transform: translate(0, -50%);
  }

  &:last-child {
    padding-right: 0;

    &::after {
      display: none;
    }
  }
}

.filter-form-toggle {
  background: none;
  display: flex;
  border: 0;
  outline: 0;
  padding: 0;
  color: $gray-dark;
  transition: $transition-base;

  &:active {
    outline: 0;
  }

  .show & {
    color: $primary;

    svg {
      color: $primary !important;
      fill: $primary !important;
    }
  }
}

.filter-body {
  display: flex;
  align-items: center;

  .j-form-field {
    flex-grow: 1;
  }
}

.filter-dropdown-toggle {
  margin-left: 12px;

  @include media-breakpoint-up(sm) {
    margin-left: 0;
  }

  &:hover{
    color: $primary;
  }

  &:active,
  &:focus {
    outline: 0;
  }

  &.toggled {
    color: $primary;
    transition: $transition-base;

    j-icon {
      svg {
        transition: $transition-base;
        color: $primary !important;
      }
    }
  }
}

.filter-dropdown-menu {
  padding: space(3) 0;
  min-width: 200px;
  border: 0;

  &.show {
    box-shadow: $box-shadow-xl;
  }
}

.filter-dropdown-item {
  padding: space(2) space(3);
  display: flex;
  align-items: center;
  line-height: 1.5;
  transition: $transition-base;
  color: $gray-dark;

  &:focus {
    outline: 0;
  }

  j-icon {
    svg {
      color: $primary !important;
    }
  }

  &:active {
    j-icon {
      svg {
        color: $white !important;
      }
    }
  }

  j-icon {
    margin-right: space(2);
  }

  &.selected {
    font-weight: $font-weight-bold;
    color: $dark;

    &:active {
      color: $white;
    }
  }
}
