.j-input {
  display: block;
  position: relative;
  background-color: $white;
  border-radius: $j-input-border-radius;
  margin-bottom: $j-input-margin-bottom;

  label.j-input--float-label {
    position: absolute;
    left: $j-input-label-position-left;
    top: $j-input-label-position-top;

    border-radius: $j-input-border-radius;
    line-height: $j-input-label-line-height;
    color: $j-input-label-color;
    user-select: none;
    z-index: $j-input-label-z-index;
    transform-origin: 0 0;
    transition: $transition-base;
    transition-delay: 0.1s;

    flex: 1;
    min-width: 0;
    max-width: calc(100% - #{$j-input-label-position-left * 2});
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
  }

  input,
  textarea,
  .j-input-control {
    display: block;
    // appearance: none;
    // -webkit-appearance: none;
    width: 100%;
    position: relative;
    z-index: $j-input-z-index;
    padding-top: $j-input-padding-top;
    padding-bottom: $j-input-padding-bottom;
    transition-delay: 0.1s;
    &,
    &:disabled,
    &:focus,
    &:hover,
    &:active {
      outline: 0;
      background: transparent;
    }
  }

  .j-input-textarea.is-filled ~ label.j-input--float-label,
  .j-input-textarea.is-focused ~ label.j-input--float-label,
  &.is-filled > label.j-input--float-label,
  textarea.is-filled ~ label.j-input--float-label,
  textarea:focus ~ label.j-input--float-label,
  textarea:active ~ label.j-input--float-label,
  input.is-filled ~ label.j-input--float-label,
  input:focus ~ label.j-input--float-label,
  input:active ~ label.j-input--float-label {
    top: $j-input-label-position-top-active;
    transform: scale(0.75);
  }

  .j-input-textarea.is-active,
  .j-input-textarea.is-focused,
  .j-input-textarea.is-focused ~ label.j-input--float-label,
  &.is-active > label.j-input--float-label,
  textarea.is-active,
  textarea:focus ~ label.j-input--float-label,
  textarea:active ~ label.j-input--float-label,
  input.is-active,
  input:focus ~ label.j-input--float-label,
  input:active ~ label.j-input--float-label {
    border-color: $j-input-border-color--active;
  }

  input:-webkit-autofill,
  input:-webkit-autofill:hover,
  input:-webkit-autofill:focus,
  textarea:-webkit-autofill,
  textarea:-webkit-autofill:hover,
  textarea:-webkit-autofill:focus,
  select:-webkit-autofill,
  select:-webkit-autofill:hover,
  select:-webkit-autofill:focus {
    border: 1px solid $orange;
    -webkit-text-fill-color: $dark;
    -webkit-box-shadow: 0 0 0px 1000px transparent inset;
    transition: background-color 5000s ease-in-out 0s,
      border-radius 0.4s ease-in-out, border-color 0.4s ease-in-out;
  }

  &.has-error {
    background: $j-input-background-color--error;
    input,
    .form-control {
      background: none;
      border-color: $j-input-border-color--error;
    }
  }

  &.disabled {
    user-select: none;
    background: $j-input-background-color--disabled;

    input,
    .form-control {
      user-select: none;
    }
  }

  &.with-icon {
    input,
    .form-control {
      padding-right: space(8);
    }
  }
}

.j-input-textarea {
  height: auto;
  padding-left: 0;
  padding-right: 4px;
  padding-bottom: 4px;

  ::-webkit-scrollbar {
    height: 8px;
    width: 8px;
  }

  /* Track */
  ::-webkit-scrollbar-track {
    background: $white;
  }

  /* Handle */
  ::-webkit-scrollbar-thumb {
    background: $gray;
    border-radius: 10px;
  }

  /* Handle on hover */
  // ::-webkit-scrollbar-thumb:hover {
  // background: #555;
  // }

  ::-webkit-resizer {
    background-color: transparent;
  }

  ::-webkit-scrollbar-corner {
    border: 0;
    background-color: transparent;
  }

  textarea {
    border: 0;
    margin-top: 12px;
    padding: 0 1.25rem 1.25rem;
    transition: none;
    // resize: none;
    // overflow: hidden;
  }
}

.j-input-extra {
  position: absolute;
  top: 8px;
  right: 16px;
  font-size: $font-size-sm;
  color: $j-input-label-color;
}

.j-input--no-label {
  input {
    padding-left: 50px;
    padding-top: $j-input-padding-bottom;
  }
}

.j-input-prefix {
  top: 20px;
  left: $j-input-label-position-left;
  position: absolute;
}

.j-input-suffix {
  z-index: $j-input-link-z-index;
  position: absolute;
  top: $j-input-link-position-top;
  right: $j-input-label-position-left;
  transition: $transition-base;
  transition-delay: 0.1s;
  @include hideable();
}

.j-input-link {
  @extend .j-input-suffix;
  cursor: pointer;
}

.j-input-shadow {
  position: absolute;
  line-height: $j-input-line-height;
  color: $j-input-placeholder-color;
  top: $j-input-placeholder-position;
  left: $j-input-shadow-position;

  .has-padding {
    width: $j-input-char-length;
    text-align: center;
    display: inline-block;
  }

  .is-invisible {
    opacity: 0;
  }

  &--date {
    left: 1.25rem;
  }
}

.j-input-button {
  outline: 0;
  border: 0;
  padding: 0;
  margin: 0;
  background: none;
  box-shadow: none;
  appearance: none;
  cursor: pointer;
  position: absolute;
  padding-right: $j-input-button-position;
  padding-left: $j-input-button-position;
  display: flex;
  align-items: center;
  justify-content: center;
  top: 0;
  right: 0;
  bottom: 0;
  width: calc(#{space(3)} + #{$j-input-button-position * 2});
  z-index: $j-input-button-z-index;
  transition: $transition-base;
  transition-delay: 0.1s;

  &,
  &:focus,
  &:hover,
  &:active {
    outline: 0;
  }

  @include hideable();

  &[disabled],
  &.is-hidden {
    z-index: $j-input-shadow-z-index;
  }

  j-icon {
    width: space(3);
    height: space(3);
    display: flex;
    align-items: center;
    justify-content: center;
    position: absolute;
  }
}

.j-input-button--shifted {
  right: calc(#{space(3)} + #{$j-input-button-position});
  z-index: $j-input-button-z-index + 1;
}

.j-input-helper {
  margin-top: -8px;
  margin-bottom: 16px;
}
.j-input-helper--errors {
  margin-top: -0.7em;
}

.j-input-spinner {
  animation: rotate 2s linear infinite;
  z-index: 2;
  position: absolute;
  top: 50%;
  left: 50%;
  margin: -12px 0 0 -12px;
  width: 24px;
  height: 24px;

  & .path {
    stroke: $primary;
    stroke-linecap: round;
    animation: dash 1s ease-in-out infinite;
  }
}

@keyframes rotate {
  100% {
    transform: rotate(360deg);
  }
}

@keyframes dash {
  0% {
    stroke-dasharray: 1, 150;
    stroke-dashoffset: 0;
  }
  50% {
    stroke-dasharray: 90, 150;
    stroke-dashoffset: -35;
  }
  100% {
    stroke-dasharray: 90, 150;
    stroke-dashoffset: -124;
  }
}

.j-input--large-textarea {
  textarea {
    min-height: 300px;
  }
}

.j-input--medium-textarea {
  textarea {
    min-height: 130px;
  }
}

.j-input-file {
  input {
    display: none;
  }

  label {
    cursor: pointer;
  }
}
