.j-list {
  margin: 1rem 0;
  display: block;
}

.j-list__nav {
  position: relative;
  border-radius: $border-radius $border-radius 0 0;

  j-contracts-nav {
    position: relative;
    z-index: 2;
  }

  &::after {
    content: '';
    z-index: 0;
    display: block;
    position: absolute;
    left: 0;
    right: 0;
    bottom: 0px;
    height: 1px;
    background: rgba($gray-500, 0.5);
  }
}

.j-list__filter {
  border-radius: $border-radius $border-radius 0 0;
  background-color: $gray-200;
  padding: 1.5rem 1rem;

  .filter-btn {
    margin-bottom: 0;
  }
}

.j-list__header {
  border-radius: $border-radius $border-radius 0 0;
  box-shadow: $box-shadow-sm;
}

.j-list__statement {
  box-shadow: $box-shadow-sm;
  background: $white;
  padding: space(3) space(4);
  border-radius: 0 0 $border-radius $border-radius;
}

.j-list__loading {
  padding: space(5) space(4);
}

.j-list__content {
  h5 {
    padding-left: 16px;
  }
}

.j-list__selectable {
  display: flex;
  height: 64px;
  padding: 16px;
  align-items: center;
  border-bottom: 1px solid $border-color;
}

.j-list__actions {
  display: flex;
  justify-content: flex-end;
}

.j-list__action {
  position: relative;
  @include btn-reset();
  color: $gray-500;
  transition: $transition-base;

  &:hover {
    color: $primary;
  }
}

.j-list__action--sign {
  margin-left: 8px;
  color: $blue-light;
}

.j-list__action:disabled,
.j-list__action--disabled {
  &,
  &:hover {
    cursor: not-allowed;
    opacity: 0.5;
    color: $gray-500;
  }
}

.j-list-item-wrap {
  position: relative;
  cursor: default;

  &.is-loading {
    .j-list-item {
      opacity: 0.25;
    }
  }
}

.j-list-item-wrap--hoverable {
  cursor: pointer;

  &::before {
    content: '';
    display: block;
    position: absolute;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    z-index: 0;
    box-shadow: $box-shadow;
    border-radius: $border-radius-md;
    opacity: 0;
    transition: $transition-base;
  }

  &:hover:not(.is-loading) {
    &::before {
      opacity: 1;
    }
  }
}

.j-list-item {
  border-radius: $border-radius-md;
  display: grid;
  column-gap: 16px;
  grid-template-columns: minmax(0, 1fr);
  padding: 16px;
  align-items: start;
  position: relative;
  z-index: 1;
  transition: $transition-base;
}

.j-list-item--highlighted {
  background-color: $blue-white;
}
