$f-page-close-width: remy(44);

.f-page {
  min-height: 100vh;
  width: 100%;
  display: flex;
  flex-direction: column;
  overflow-x: hidden;
}

.f-page-close {
  width: $f-page-close-width;
  height: $f-page-close-width;
  top: 0;
  left: 0;
  padding: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-bottom: 1rem;

  @include media-breakpoint-up(lg) {
    position: absolute;
  }
}

.f-page-close--outside {
  @include media-breakpoint-up(lg) {
    left: calc(-1 * (#{$spacer} + #{$f-page-close-width}));
  }
}

.f-page-header {
  position: relative;
  padding-top: 1rem;
  @include media-breakpoint-up(lg) {
    padding-top: space(6);
  }
}

.f-page-title {
  line-height: 40px;
  margin-bottom: 28px;

  @include media-breakpoint-up(md) {
    line-height: 60px;
  }
}

.f-page-body {
  background: $gray-200;
  padding: space(3) 0;

  @include media-breakpoint-up(md) {
    padding: space(4) 0;
    flex-grow: 1;
  }

  &.no-grow {
    flex-grow: unset;
  }
}

.f-page-container {
  position: relative;
}

.f-page-tabs-wrap {
  position: relative;
  overflow: hidden;
  height: 55px;
}

.f-page-tabs {
  display: flex;
  position: absolute;

  a {
    height: 55px;
    padding: 0.7em 0;
    color: $gray-600;
    font-size: $h6-font-size;
    border-bottom: 2px solid transparent;
    transition: $transition-base;
    white-space: nowrap;

    & + a {
      margin-left: space(6);
    }

    &:hover {
      border-color: $primary;
    }

    &.active {
      color: $dark;
      border-color: $primary;
      font-weight: $font-weight-bold;
    }
  }
}

.f-page-wrapper {
  position: fixed;
  width: 100%;
  bottom: 0;
  top: 0;
  z-index: 100;
  right: 0;
  overflow-x: hidden;
  // transition: all 0.4s ease-out;
  visibility: hidden;
  opacity: 0;
  transition: visibility 0s, opacity 0.25s ease-out;

  &.is-shown {
    visibility: visible;
    opacity: 1;
  }
}

.f-page-backdrop {
  content: '';
  display: block;
  position: absolute;
  left: 0;
  bottom: 0;
  top: 0;
  right: 0;
  background-color: rgba(246, 247, 248, 0.8);
  z-index: 5;
}

.f-page-content {
  z-index: 10;
  right: -100%;
  top: 0px;
  bottom: 0;
  position: absolute;
  background: $white;
  transition: all 0.25s ease-out;
  width: 100%;
  overflow-y: auto;

  // @include media-breakpoint-up(md) {
  //   padding-top: $header-height-md;
  // }

  @include media-breakpoint-up(lg) {
    padding-top: 0;
  }

  &--quarter {
    border-radius: space(4) 0px 0px space(4);
    box-shadow: $box-shadow-xl;

    @include media-breakpoint-up(md) {
      width: 25%;
      min-width: 435px;
    }
  }

  &.is-shown {
    right: 0;
  }

  &--gray {
    background: $gray-200;
  }
}

.f-page__wrapper {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(246, 247, 248, 0.8);
  z-index: 5000;
}

.f-page__aside {
  top: 0;
  right: 0;
  bottom: 0;
  position: absolute;
  width: 25%;
  min-width: 425px;
  background: #f6f7f8;
  box-shadow: 0px 24px 32px rgba(0, 0, 0, 0.04),
    0px 16px 24px rgba(0, 0, 0, 0.04), 0px 4px 8px rgba(0, 0, 0, 0.04),
    0px 0px 1px rgba(0, 0, 0, 0.04);
  border-radius: 12px 0px 0px 12px;
  overflow: hidden;
}

.f-page__header {
  background: $white;
  padding: space(4);
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.f-page__block {
  background: $white;
  border-radius: $border-radius;
  padding: space(3);
  margin: remy(12) 0;

  &--collapsible {
    padding-top: 0;
    padding-bottom: 0;
    transition: $transition-base;
  }
}

.f-page__scrollable {
  flex-grow: 1;
  overflow-y: auto;
  padding: space(3) space(4);

  /* width */
  &::-webkit-scrollbar {
    width: 8px;
  }

  /* Track */
  &::-webkit-scrollbar-track {
    background: $white;
  }

  /* Handle */
  &::-webkit-scrollbar-thumb {
    background: $gray-400;
    border-radius: 10px;
  }
}

.f-page__close {
  @include btn-reset();

  color: $gray-600;

  &:hover {
    color: $primary;
  }
}

.f-page__heading {
  font-size: 20px;
  font-weight: 500;
}
