.j-desc-list {
  list-style: none;
  margin: 0;
  padding: 0;
}

.j-desc-heading{
  font-size: remy(20);
  font-weight: $font-weight-bold;
}


.j-desc-row {
  display: grid;
  grid-template-columns: 2fr 3fr;
}

.j-desc-row--loading {
  column-gap: 2rem;
}

.j-desc-row + .j-desc-row {
  margin-top: remy(12);
}

.j-desc-child {
  &:first-child {
    text-align: left;
  }

  &:last-child {
    text-align: right;
  }
}
