.auth-wrap {
  // margin-top: $header-height;
  overflow-x: hidden;
  display: flex;
  align-items: stretch;
  min-height: 100vh;
  flex-direction: column;
  justify-content: center;
  background: $gray-200;

  @include media-breakpoint-up(sm) {
    background: none;
    min-height: 100vh;
  }
}

.register-wrap {
  @include media-breakpoint-up(sm) {
    padding-left: $header-width;
  }
}

.auth-content {
  position: relative;
  z-index: 5;
  background: $gray-200;
  min-height: calc(100vh - #{$header-height});
  display: flex;

  @include media-breakpoint-up(sm) {
    min-height: 100vh;
    border-radius: 40px 0px 0px 40px;
    box-shadow: $box-shadow-xl;
  }
}

.auth-submit {
  display: flex;
  text-align: center;
  align-items: center;
  flex-direction: column;
  margin-top: space(5);

  .btn {
    width: 100%;
  }

  .btn + .btn {
    margin-top: space(2);
  }

  @include media-breakpoint-up(sm) {
    flex-direction: row;

    .btn {
      // width: auto;
      white-space: nowrap;
    }

    .btn + .btn {
      margin-top: 0;
      margin-left: space(3);
    }
  }
}

.auth-submit__button {
  height: 100%;
  width: 100%;
}

.auth-submit__button + .auth-submit__button {
  margin-top: space(3);

  @include media-breakpoint-up(sm) {
    margin-top: 0;
    margin-left: space(3);
  }
}

.auth-heading {
  // line-height: 1;
}

.auth-form {
  position: relative;
  min-height: 300px;
}

.auth-backlink {
  position: absolute;
  top: -#{space(7)};
}
