.j-nav {
  margin-top: auto;
  margin-bottom: auto;
  // padding-top: 8vh;
  // padding-bottom: 8vh;
  width: 100%;
  // padding-left: space(6);

  j-nav-item {
    display: block;
    margin-bottom: 12px;
    margin-right: 1rem;

    &:last-child {
      margin-bottom: 0;
    }
  }

  .nav-item {
    display: flex;
    align-items: center;
    justify-content: flex-start;
    color: $gray;
    height: 40px;
    padding-left: space(6);
    position: relative;
    margin-bottom: 12px;

    &:last-child {
      margin-bottom: 0;
    }

    &.disabled {
      opacity: 0.5;
      cursor: not-allowed;
    }

    &.active {
      color: $dark;
      font-weight: $font-weight-bold;
    }

    &.current {
      &::before {
        width: 2px;
      }
    }

    &:hover {
      &::before {
        width: 2px;
      }
    }

    &.passed {
      color: $dark;
    }

    &::before {
      content: '';
      display: block;
      position: absolute;
      width: 0px;
      left: space(6);
      left: 0;
      top: 0;
      bottom: 0;
      background: $primary;
      transition: $transition-base;
    }

    j-icon {
      position: absolute;
      left: 0;

      @include hideable();
    }

    .badge {
      min-width: 30px;
      min-height: 18px;
      position: relative;
      display: block;

      .spinner {
        div {
          width: 4px;
          height: 4px;
          margin-right: 2px;
        }
      }
    }

    &--icon-padding {
      padding-left: space(7);
      j-icon {
        left: space(4);
      }
    }
  }

  .nav-item__child {
    display: flex;
    align-items: center;
    justify-content: flex-start;
    color: $gray;
    height: space(5);
    position: relative;

    &.disabled {
      opacity: 0.5;
      cursor: not-allowed;
    }

    &.active {
      color: $dark;
      font-weight: $font-weight-bold;
    }

    &.current {
      &::before {
        width: 2px;
      }
    }

    &.passed {
      color: $dark;
    }
  }
}
