$j-modal-padding: space(4);
$j-modal-padding-lg: space(7);

.j-modal-header.modal-header {
  position: relative;
  padding: $j-modal-padding;

  @include media-breakpoint-up(sm) {
    padding: $j-modal-padding-lg $j-modal-padding-lg 0;
  }

  button.close {
    padding: 0;
    outline: 0;
    margin: 0 0 0 auto;
    position: absolute;
    right: $j-modal-padding;
    top: $j-modal-padding;
  }
}

.j-modal-body.modal-body {
  padding: 0 $j-modal-padding;

  @include media-breakpoint-up(sm) {
    padding: 0 $j-modal-padding-lg;
  }
}

.j-modal-footer.modal-footer {
  justify-content: flex-start;
  padding: $j-modal-padding;
  flex-wrap: nowrap;

  @include media-breakpoint-up(sm) {
    padding: $j-modal-padding $j-modal-padding-lg $j-modal-padding-lg;
  }

  .btn {
    margin: 0;
  }

  .btn + .btn {
    margin-left: space(3);
  }
}
.backdrop_light {
  background-color: rgba(246, 247, 248);
}

.modal_light {
  .close {
    z-index: 1;
    position: fixed;
    margin: 0;
    top: 2vh;
    right: 2vw;
  }
  .modal-content {
    box-shadow: $box-shadow-xl;
  }
}

.modal_max-height {
  .modal-content {
    max-height: 90vh;
    max-height: calc(100vh - 3rem);
  }
}

.modal-adaptive {
  .modal-dialog {
    margin: 0px;
    max-width: 100%;
    height: 100%;
    @include media-breakpoint-up(md) {
      margin: 1.75rem auto;
      max-width: calc(100% - 6rem);
      height: auto;
    }
    &.modal-lg {
      @include media-breakpoint-up(lg) {
        max-width: $modal-lg;
      }
    }
  }
  .modal-body {
    @include media-breakpoint-up(md) {
      height: auto;
    }
  }
  .modal--close {
    display: inline-block;
    .close {
      display: none;
      @include media-breakpoint-up(md) {
        display: block;
      }
    }
    .btn {
      padding: 0.25rem;
      min-width: 2em;
      margin: space(3) 0 0 space(3);
      @include media-breakpoint-up(md) {
        display: none;
      }
    }
  }
  .modal-back {
    width: 40px;
    height: 40px;
  }
}

.modal-adaptive_sm {
  .modal-dialog {
    max-width: 582px;
  }
}
.modal-holder{
  animation-name: holder;
  animation-duration: 0.3s;
}

@keyframes holder {
  0%   {transform: scale(0.5)}
  100% {transform: scale(1)}
}

.modal_sm {
  .modal-dialog {
    @include media-breakpoint-up(sm) {
      max-width: 380px;
    }
    @include media-breakpoint-up(xl) {
      max-width: 450px;
    }
  }
  .modal-content {
    @include media-breakpoint-up(sm) {
      border-radius: 2.6rem;
      max-width: 380px;
    }
    @include media-breakpoint-up(xl) {
      max-width: 450px;
    }
  }
}

.modal_dictionary {
  &.modal-dialog,
  .modal-dialog {
    @include media-breakpoint-up(sm) {
      max-width: 582px;
    }
  }

  .modal-content {
    @include media-breakpoint-up(sm) {
      max-width: 582px;
    }
  }
}

.modal_md {
  .modal-dialog {
    @include media-breakpoint-up(sm) {
      max-width: 680px;
    }
  }
  .modal-content {
    @include media-breakpoint-up(sm) {
      border-radius: 2.6rem;
      max-width: 680px;
    }
  }
}

.modal-warning {
  background: $red-light;
  color: $red;
  padding: 12px 100px;
  text-align: center;
}
