body.mobile .mobile-panel {
  display: flex;

  @include media-breakpoint-up(sm) {
    display: none;
  }
}

body.mobile .aside {
  top: 72px;

  @include media-breakpoint-up(sm) {
    top: 0;
  }
}

body.mobile .auth-wrap {
  min-height: calc(100vh - 72px);

  @include media-breakpoint-up(sm) {
    min-height: 100vh;
  }
}

body.mobile .f-page-content {
  top: 72px;

  @include media-breakpoint-up(sm) {
    top: 0;
  }
}

body.mobile .layout-home--header {
  top: 72px;

  @include media-breakpoint-up(sm) {
    top: 0;
  }
}

body.mobile .filter-aside {
  top: 72px;

  @include media-breakpoint-up(sm) {
    top: 0;
  }
}

body.mobile .filter-aside.extra-space {
  top: calc(62px + 72px);

  @include media-breakpoint-up(sm) {
    top: 62px;
  }
}

body.mobile .filter-aside-header {
  top: 72px;

  @include media-breakpoint-up(sm) {
    top: 0;
  }
}

body.mobile .filter-aside.extra-space .filter-aside-header {
  top: calc(65px + 72px);

  @include media-breakpoint-up(sm) {
    top: 65px;
  }
}

body.mobile .filter-aside-body {
  max-height: calc(100vh - 64px - 72px);

  @include media-breakpoint-up(sm) {
    max-height: calc(100vh);
  }
}

body.mobile .filter-aside.extra-space .filter-aside-body {
  max-height: calc(100vh - 124px - 72px);

  @include media-breakpoint-up(sm) {
    max-height: calc(100vh - 124px);
  }
}
