.tooltip-light{
  z-index: 10000;
}

.tooltip-light .tooltip-inner {
  max-width: unset;
  opacity: 1;
  padding: 0.5rem 1rem;
  background-color: $gray-100;
  font-size: $font-size-xs;
  color: $dark;
  box-shadow: 0px 10px 20px rgba(0, 0, 0, 0.1), 0px 2px 6px rgba(0, 0, 0, 0.1),
    0px 0px 1px rgba(0, 0, 0, 0.1);
}

.tooltip-light.bs-tooltip-top {
  .arrow {
    &::before {
      border-top-color: $gray-100;
    }
  }
}

.tooltip-light.bs-tooltip-right {
  .arrow {
    &::before {
      border-right-color: $gray-100;
    }
  }
}

.tooltip-light.bs-tooltip-bottom {
  .arrow {
    &::before {
      border-bottom-color: $gray-100;
    }
  }
}

.tooltip-light.bs-tooltip-left {
  .arrow {
    &::before {
      border-left-color: $gray-100;
    }
  }
}



.tooltip-large {
  width: 300px;

  .tooltip-inner {
    padding-top: 1rem;
    text-align: left;
  }
}

.tooltip-medium {
  width: 250px;

  .tooltip-inner {
    padding-top: 1rem;
    text-align: left;
  }
}
