$j-sms-input-font-size: 48px;
$j-sms-input-letter-spacing: 10px;
$j-sms-input-char-width: 36.95px;
$j-sms-input-max-width: 230px;
$j-sms-input-shadow-height: 56px;

.j-sms-input {
  background: transparent;
  max-width: $j-sms-input-max-width;

  input {
    &,
    &:focus {
      background: transparent;
      appearance: none;
      box-shadow: none;
      border: 0;
      outline: none;
      padding: 0;
      border-radius: 0;
      font-size: $j-sms-input-font-size;
      letter-spacing: $j-sms-input-letter-spacing;
    }
  }

  .j-input-shadow {
    height: $j-sms-input-shadow-height;
    line-height: $j-sms-input-shadow-height;
    top: 0;
    padding: 0;
    left: 0;
    font-size: $j-sms-input-font-size;
    letter-spacing: $j-sms-input-letter-spacing;

    span {
      text-align: center;
      display: inline-block;
      width: $j-sms-input-char-width;
    }
  }
}
