.saved-employee {
  width: 100%;
  display: block;
  background: $gray-200;
  padding: 1rem;
  border-radius: 8px;
  margin-bottom: 1rem;
  transition: $transition-base;
  position: relative;

  &:hover {
    background: $gray-800;
  }


  .saved-name {
    display: block;
    line-height: 24px;
  }
  .employee-delete-confirm {
    padding: 0.5rem 1rem;
  }
}

.saved-employee-actions {
  position: absolute;
  right: 1rem;
  top: 20px;
  display: flex;
  z-index: 1;
}

.saved-employee-action {
  margin-left: 12px;
  cursor: pointer;

  .saved-employee-icon {
    display: block;
  }
  .saved-employee-icon--hover {
    display: none;
  }

  &:hover {
    .saved-employee-icon {
      display: none;
    }
    .saved-employee-icon--hover {
      display: block;
    }
  }
}

.employee-delete-confirm {
  display: flex;
  justify-content: space-between;
  padding: 0rem 1rem 1.5rem;
}

.employee-delete-link {
  display: flex;
  &,
  &:hover {
    color: $blue-light;
  }
}

.employee-delete-icon {
  margin-right: 0.5rem;
}
