.form-section {
  padding-bottom: 1.5rem;
  border-bottom: 1px solid rgba($gray-500, 0.5);
  & + & {
    margin-top: 1.5rem;
  }
  .h2 {
    margin-bottom: 1em;
  }
  .form-group:last-child {
    margin-bottom: 0px;
    .j-input {
      margin-bottom: 0px;
    }
  }
  .list-group {
    border: 0px;
    .j-switch label {
      margin: 0;
    }
  }
  .list-group-item + .list-group-item {
    border-top-width: 1px;
  }
}
