.btn {
  position: relative;
  cursor: pointer;
  max-height: 56px;
  backface-visibility: hidden;
  transform-style: preserve-3d;
  transform: scale(1);
  outline: none;
  box-shadow: none;
  will-change: transform;

  &:not(.btn-frozen):hover {
    transform: scale(1.05);
  }

  &:active {
    transform: scale(0.95);
  }

  &:focus,
  &:active {
    outline: none;
  }

  &[disabled],
  &.disabled {
    &,
    &:hover,
    &:active,
    &:focus {
      box-shadow: none;
      transform: scale(1);
      user-select: none;
      opacity: 0.5;
      cursor: not-allowed;
    }
  }

  &:not(.dropdown-toggle)::after {
    content: '';
    opacity: 0;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    visibility: hidden;
    background-image: url();
  }

  &.is-loading {
    &,
    &:hover,
    &:active,
    &:focus {
      cursor: default;
      transform: scale(1);
      user-select: none;
      color: transparent !important;
    }

    :not(.spinner) {
      opacity: 0;
    }

    .spinner {
      opacity: 1;
    }
  }
}
.btn-static {
  min-width: $btn-min-width;
}

.btn-outline-primary {
  &:hover,
  &:active {
    color: $orange-dark !important;
    border-color: $orange-dark !important;
    background: transparent !important;
  }
}
.btn-outline-dark {
  border-color: $gray-400;
}

.btn-outline-dark-deep {
  border-color: $gray-600;
}

.btn-dashed {
  border-style: dashed;
}
.btn-transparent {
  &:hover,
  &:active,
  &:focus {
    background-color: transparent;
  }
}

.btn-link {
  &,
  &:hover,
  &:active,
  &:focus {
    transform: scale(1) !important;
  }

  &:hover {
    color: $orange-dark;
  }
}

.btn-shadow {
  box-shadow: $box-shadow;
  &:hover,
  &:active,
  &:focus {
    box-shadow: none;
  }
}

.btn-icon {
  display: flex;
  width: 48px;
  height: 48px;
  align-items: center;
  justify-content: center;
  padding: 0;
}

.btn-xs {
  @include button-size(
    $btn-padding-y * 0.5,
    $btn-padding-x * 0.5,
    $btn-font-size * 0.8,
    $btn-line-height * 0.5,
    $btn-border-radius
  );
}

.btn-xxs {
  @include button-size(
    $btn-padding-y * 0.3,
    $btn-padding-x * 0.16,
    $btn-font-size * 0.4,
    $btn-line-height * 0.3,
    $btn-border-radius
  );
}

.btn-md {
  @include button-size(
    remy(11),
    remy(12),
    $btn-font-size,
    $btn-line-height,
    $btn-border-radius
  );
}

.btn-as-text {
  padding: 0px;
}
.btn-link-gray {
  color: $gray-600;
  &:hover,
  &:focus,
  &:active {
    color: $primary;
  }
}

.btn-link-light-gray {
  color: $gray-500;
  &:hover,
  &:focus,
  &:active {
    color: $primary;
  }
}

.btn-link-classic {
  color: $blue-light;
  background: transparent;
  border: 0;
  &:hover {
    text-decoration: underline;
  }
}

.btn-gray-main {
  background: $gray-200;
  color: $black;
}

button {
  &:focus,
  &:active {
    outline: none;
  }
}

.btn-no-border {
  border: 0;
}
